import React, { Component } from 'react';
import { Row, Col, Card } from 'antd';
import { inject, observer } from 'mobx-react';
import PageWrapper from 'components/PageWrapper';
import FormatterLocale from 'components/FormatterLocale';
import ListTable from './ListTable';
import styles from './listTable.module.scss';

@inject('layoutStore')
@observer
class BasicList extends Component {
  render() {
    const list = [{
      title: '',
      num: '8'
    }, {
      title: '',
      num: '32'
    }, {
      title: '',
      num: '11'
    }];
    const RowInfo = () => (
      <Row gutter={24}>
        {list.map(col => {
          return (
            <Col xl={8} sm={24} xs={24} key={col.title}>
              <Card bordered={false}>
                <p className={styles.colTitle}>{col.title}</p>
                <span className={styles.colNum}>{col.num}</span>
              </Card>
            </Col>
          )
        })}
      </Row>
    );

    const DontShow = () => (
      <Card style={{ marginTop: '24px' }} bordered={false}>
        <div style={{ textAlign: 'center' }}></div>
      </Card>
    )

    const { isMobile } = this.props.layoutStore;
    return (
      <PageWrapper title={<FormatterLocale id="basicList.title" />}>
        <RowInfo />
        {isMobile ? <DontShow /> : <ListTable />}
      </PageWrapper>
    );
  }
}

export default BasicList;