import React, { Component } from 'react';
import { Card, Input, Radio, List, Avatar, Progress, Button } from 'antd';
import { getListData } from 'api/list';
import { getContact } from 'api/platform';
import styles from './listTable.module.scss';

const Search = Input.Search;
const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

class ListTable extends Component {
  state = {
    list: []
  }

  componentDidMount() {
    this.initData();
  }

  async initData() {
    const { data: { results } } = await getContact();
    let { data: { data: _data } } = await getListData();
    _data = _data.map((res, index) => {
      return {
        ...res,
        avatar: results[index].picture.thumbnail
      }
    });
    this.setState({
      list: _data
    })
  }

  handleSearch = () => {
    // console.log(value);
  }

  handleChange = () => {
    // console.log(`radio checked:${e.target.value}`);
  }

  render() {
    const { list } = this.state;
    const Extra = (
      <div>
        <RadioGroup onChange={this.handleChange} defaultValue="a">
          <RadioButton value="a"></RadioButton>
          <RadioButton value="b"></RadioButton>
          <RadioButton value="c"></RadioButton>
        </RadioGroup>
        <Search
          placeholder=""
          onSearch={this.handleSearch}
          style={{ width: 200, marginLeft: '8px' }}
        />
      </div>
    )

    const ListContent = ({ data: { handler, date, progress } }) => (
      <div className={styles.content}>
        <div>
          <span></span>
          <p className={styles.pText}>{handler}</p>
        </div>
        <div className={styles.handleTime}>
          <span></span>
          <p className={styles.pText}>{date}</p>
        </div>
        <Progress showInfo={false} percent={progress} className={`${styles.progress} ${progress < 40 ? 'warning' : 'success'}`} />
        <span>{progress}%</span>
      </div>
    )

    return (
      <Card bordered={false}
        style={{ marginTop: '24px' }}
        className="fat-card"
        extra={Extra} title=""
      >
        <List rowKey="id"
          loading={!list.length}
          dataSource={list}
          renderItem={item => (
            <List.Item actions={[<Button key="first" type="link"></Button>, <Button key="second" type="link"></Button>]}>
              <List.Item.Meta
                avatar={<Avatar src={item.avatar} shape="square" size="large" />}
                title={<Button type="link">{item.title}</Button>}
                description={<div className={styles.detail}>{item.detail}</div>}
              />
              <ListContent data={item} />
            </List.Item>
          )
          }
        />
      </Card>
    )
  }
}

export default ListTable;